import React, { useState } from 'react';
import PropTypes from 'prop-types';
import BannerComponent from './BannerComponent';
import FeaturedProducts from './FeaturedProducts';
import CategoryBrandComponet from './CategoryBrandComponent';
import ProductByCategory from './ProductByCategory';
import { MainContainer, SubscribeContainer, SubscribeForm, SubscribeDetails } from '../style';
import { Container } from '../../../style';
import { products, FruitsAndVegitables, HomeAppl } from '../mockData';

import banner1 from '../../../assets/images/bg_1.jpg';
import banner2 from '../../../assets/images/bg_2.jpg';
import banner3 from '../../../assets/images/mango.png';
import banner4 from '../../../assets/images/organic.png';
export const DetectPhone = () => {
    const currentDevive = navigator.userAgent.toLowerCase();
    if (currentDevive.search('iphone') > -1) {
        console.log('iphone');
        return 'iphone';
    } else if (currentDevive.search('ipad') > -1) {
        console.log('ipad');
        return 'ipad';
    } else {
        console.log('web');
        return 'web';
    }
};
const propTypes = {};
const Home = () => {
    const bannerImages = [banner1, banner2];
    const bannerImages1 = [banner3, banner4];

    const [option, setOption] = useState({
        arrows: true,
        rewind: true,
        perPage: 4,
        pagination: false,
        autoplay: false,
        perMove: 3,
        interval: 3000,
    });
    window.onresize = () => {
        const d = DetectPhone();
        setOption({
            ...option,
            perPage: d === 'web' ? 4 : d === 'iphone' ? 1 : d === 'ipad' ? 2 : 4,
            perMove: d === 'web' ? 4 : d === 'iphone' ? 1 : d === 'ipad' ? 2 : 4,
        });
        console.log(option, 'state value');
    };
    return (
        <MainContainer>
            <BannerComponent
                options={{
                    ...option,
                    arrows: false,
                    perPage: 1,
                    autoplay: true,
                    perMove: 1,
                    gap: '1rem',
                }}
                bannerImages={bannerImages}
            />
            <FeaturedProducts options={option} heading="Featured Products" showDeatils={true} key={`${option}`} />
            <CategoryBrandComponet options={{ ...option, gap: '1rem' }} type="category" />
            <Container>
                <BannerComponent
                    options={{
                        ...option,
                        arrows: false,
                        perPage: 1,
                        autoplay: true,
                        perMove: 1,
                        gap: '1rem',
                    }}
                    bannerImages={bannerImages1}
                />
            </Container>
            <ProductByCategory heading="Fruit and Vegitables" products={FruitsAndVegitables} />
            <CategoryBrandComponet options={{ ...option, gap: '1.5rem' }} type="brand" />
            <ProductByCategory heading="Home appliances" products={HomeAppl} />
            <FeaturedProducts options={option} heading="Snacks & Branded Food" showDeatils={false} />
            <SubscribeContainer>
                <Container>
                    <div className="row">
                        <SubscribeDetails>
                            <h2>Subcribe to our Newsletter</h2>
                            <span>Get e-mail updates about our latest shops and special offers</span>
                        </SubscribeDetails>
                        <SubscribeForm>
                            <input type="text" placeholder="Email Address" />
                            <button>Subscribe</button>
                        </SubscribeForm>
                    </div>
                </Container>
            </SubscribeContainer>
        </MainContainer>
    );
};
Home.propTypes = propTypes;
export default Home;
