import React, { useState, useEffect, useReducer } from 'react';
import Slider from '../../../components/Slider';
import { SplideSlide } from '@splidejs/react-splide';
import { FaShoppingCart } from 'react-icons/fa';

import { FearturedContainer, OfferTag, ImageContainer, ProductDetails } from '../style';
import { products } from '../mockData';
import { Container } from '../../../style';
import Product from './Product';

type optionsType = {
    options: any;
    showDeatils: boolean;
    heading: string;
};
const FeaturedProducts = (props: optionsType) => {
    const { options, showDeatils, heading } = props;
    return (
        <FearturedContainer>
            <Container>
                <div className="heading">
                    <h3>{heading}</h3>
                </div>
                <Slider options={options} key={`${options.perPage}perMove`}>
                    {products.map((product, index) => {
                        return (
                            <SplideSlide key={index}>
                                <div className="product-div">
                                    <Product product={product} showDeatils={showDeatils} />
                                </div>
                            </SplideSlide>
                        );
                    })}
                </Slider>
            </Container>
        </FearturedContainer>
    );
};
export default FeaturedProducts;
